<template>
  <div class="details">
      <div class="row mb-5">
        <div style="padding-left: 0px; padding-right: 0px" class="col-md-12" v-for="(product,index) in products" :key="index">
          <div v-if="proId == product.productId">
            <img :src="product.image" class="img-fluid">
            <h4 class="titulo-hospital">{{product.productTitle}}</h4>
            <div class="card m-3">
              <div class="card-text p-2" style="text-align: justify">
                {{product.productDescription}}
              </div>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
    export default {
        name: 'details',
        data() {
            return {
                proId: this.$route.params.Pid,
                title: "details",
                products: [
                    {
                        productTitle: "Hospital Infantil João Paulo II",
                        productDescription: "O Hospital Infantil João Paulo II (HIJPII), localizado no bairro Santa Efigênia, em Belo Horizonte, foi fundado em 1971 como Centro Hospitalar de Doenças Transmissíveis (CHDT).",
                        image: require('../assets/images/hosp_1.jpg'),
                        productId: 1
                    },
                    {
                        productTitle: "IPSEMG Hospital Governador Israel Pinheiro HGIP",
                        productDescription: "Inaugurado em 18 de fevereiro de 1971, o Hospital Governador Israel Pinheiro (HGIP), presta assistência à saúde aos servidores do Estado e seus dependentes.",
                        image: require('../assets/images/hosp_2.jpg'),
                        productId: 2
                    },
                    {
                        productTitle: "Hospital João XXIII",
                        productDescription: "DescriçãoO Hospital João XXIII é um hospital público estadual de pronto socorro em Belo Horizonte, da Fundação Hospitalar do Estado de Minas Gerais - FHEMIG, que operacionaliza o SUS Sistema Único de Saúde em seu nível estadual, realizando atendimentos de alta complexidade em urgência e emergência.",
                        image: require('../assets/images/hosp_3.jpg'),
                        productId: 3
                    },
                    {
                        productTitle: "Hospital São Lucas",
                        productDescription: "Nos últimos anos, vem passando por grandes reformas, incorporando instalações de alto nível com elevado padrão de hotelaria.",
                        image: require('../assets/images/hosp_4.jpg'),
                        productId: 4
                    },
                    {
                        productTitle: "Hospital Maria Amélia Lins Fhemig",
                        productDescription: "O Hospital Maria Amélia Lins (HMAL), localizado no bairro Santa Efigênia, em Belo Horizonte, foi inaugurado em 1947.\n" +
                            "Ao iniciar suas atividades, o HMAL tinha a missão de atuar como pronto-socorro e Instituto Médico Legal de Belo Horizonte.",
                        image: require('../assets/images/hosp_5.jpg'),
                        productId: 5
                    },
                    {
                        productTitle: "Matermed",
                        productDescription: "Desde 15 de agosto de 1971, nossa missão é promover qualidade de vida na região da Grande Belo Horizonte, oferecendo uma assistência médico-hospitalar de alto padrão.",
                        image: require('../assets/images/hosp_6.jpg'),
                        productId: 6
                    }
                ]
            }
        }

    }
</script>

<style>
  .details{
    width: 100% !important;
  }
  .titulo-hospital{
    margin-top: 1em;
  }
</style>



