<template>
  <div id="app">
    <!-- navbar -->
    <b-navbar toggleable="lg" type="dark" variant="info">
      <b-navbar-brand href="#">
        <router-link class="nav-link menus-link" to="/">SaúdeAgora</router-link>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <b-nav-item href="#">
            <router-link class="nav-link" to="/blog">Blog</router-link>
          </b-nav-item>
        </b-navbar-nav>

        <b-navbar-nav>
          <b-nav-item href="#">
            <router-link class="nav-link" to="/services">Services</router-link>
          </b-nav-item>
        </b-navbar-nav>

        <b-navbar-nav>
          <b-nav-item href="#">
            <router-link class="nav-link" to="/contact">contact</router-link>
          </b-nav-item>
        </b-navbar-nav>

      </b-collapse>
    </b-navbar>
    <div class="container-fluid">
      <transition name="moveInUp">
        <router-view/>
      </transition>

    </div>

  </div>
</template>

<script>

    export default {
        name: 'App'
    }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  .menus-link{
    color: #ffffff;
    font-weight: lighter;
  }

  a:link{
    text-decoration: none;
    color: #ffffff;
    font-weight: lighter;
  }
  a:visited{
    text-decoration: none;
    color: #ffffff;
    font-weight: lighter;
  }
  a:hover{
    text-decoration: none;
    color: #ffffff;
    font-weight: lighter;
  }
  a:active{
    text-decoration: none;
    color: #ffffff;
    font-weight: lighter;
  }

  .moveInUp-enter-active {
    animation: fadeIn 2s ease-in;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 0.5;
    }
    100% {
      opacity: 1;
    }
  }

  .moveInUp-leave-active {
    animation: moveInUp .3s ease-in;
  }

  @keyframes moveInUp {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(-400px);
    }
  }
</style>



